import {
  Transaction,
  Title,
  Tableolumns,
  TableRows,
} from './TransactionHistory.stylrd';
import PropTypes from 'prop-types';

export const TransactionHistory = ({ items }) => {
  return (
    <Transaction>
      <thead>
        <tr>
          <Title>Type</Title>
          <Title>Amount</Title>
          <Title>Currency</Title>
        </tr>
      </thead>

      <tbody>
        {items.map(({ id, type, amount, currency }) => (
          <TableRows key={id}>
            <Tableolumns>{type}</Tableolumns>
            <Tableolumns>{amount}</Tableolumns>
            <Tableolumns>{currency}</Tableolumns>
          </TableRows>
        ))}
      </tbody>
    </Transaction>
  );
};

TransactionHistory.propTypes = {
  id: PropTypes.string,
  type: PropTypes.string,
  amount: PropTypes.string,
  currency: PropTypes.string,
};
